*,*::before,*::after{font-family:"Montserrat", sans-serif;box-sizing:border-box}body,h1,h2,h3,h4,h5,h6,p{margin:0}a,a:visited,a:active{color:inherit;text-decoration:none}h1,h2,h3,h4{font-weight:700}h5,h6{font-weight:500}body{color:#eee;background-color:#252525}.content{width:85vw;margin:5rem auto}.row{display:flex;justify-content:space-between}.column{display:flex;flex-direction:column}h1{font-size:6rem}h2{font-size:4.5rem}h3{font-size:3.5rem}h4{font-size:2rem}h5{font-size:1.5rem}h6,p{font-size:1.2rem}

.App .row .employees{width:69%;overflow-x:auto}.App .row .employees .column{width:15rem;margin:0 1rem}.App .row .employees .column h5{margin:0 0 1rem 0;text-transform:uppercase}.App .row .birthday{width:29%;padding:0 1rem;border-left:solid 0.1rem}.App .row .birthday .month{margin:0 0 2rem 0}.App .row .birthday .month li{margin:0.8rem 0;color:#5858ff}.App .row h4{margin:0 0 2rem 2rem}

.employee{margin:0.5rem 0}.employee h6{width:-webkit-max-content;width:max-content}.employee h6.blue{color:#5858ff}.employee label{margin:0.5rem 0;display:block}

